"use client";

import { useSearchParams } from "next/navigation";
import { useState } from "react";

export default function ReservationPage() {
  const searchParams = useSearchParams();
  const selectedPackage = searchParams.get("package") || "";

  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
    travelDate: "",
    travelers: "",
    notes: "",
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  return (
    <main className="min-h-screen bg-gradient-to-b from-sky-100 via-white to-sky-50 flex items-center justify-center px-4 py-16">
      <div className="w-full max-w-3xl bg-white/70 backdrop-blur-md shadow-2xl rounded-2xl p-10 border border-sky-100 transition-all duration-500 hover:shadow-sky-200/50">
        <h1 className="text-3xl font-extrabold text-center text-sky-700 mb-8">
          Reserve Your Trip
        </h1>

        {selectedPackage && (
          <p className="text-center text-gray-700 mb-6">
            Booking for:{" "}
            <span className="font-semibold text-sky-700">{selectedPackage}</span>
          </p>
        )}

        <form className="space-y-5">
          <div className="grid md:grid-cols-2 gap-5">
            <input
              name="name"
              type="text"
              placeholder="Full Name"
              value={formData.name}
              onChange={handleChange}
              className="w-full px-4 py-3 rounded-lg border border-gray-200 bg-white/70 focus:outline-none focus:ring-2 focus:ring-sky-500 transition"
            />

            <input
              name="email"
              type="email"
              placeholder="Email Address"
              value={formData.email}
              onChange={handleChange}
              className="w-full px-4 py-3 rounded-lg border border-gray-200 bg-white/70 focus:outline-none focus:ring-2 focus:ring-sky-500 transition"
            />
          </div>

          <div className="grid md:grid-cols-2 gap-5">
            <input
              name="phone"
              type="text"
              placeholder="Phone Number"
              value={formData.phone}
              onChange={handleChange}
              className="w-full px-4 py-3 rounded-lg border border-gray-200 bg-white/70 focus:outline-none focus:ring-2 focus:ring-sky-500 transition"
            />

            <input
              name="travelDate"
              type="date"
              placeholder="Preferred Travel Date"
              value={formData.travelDate}
              onChange={handleChange}
              className="w-full px-4 py-3 rounded-lg border border-gray-200 bg-white/70 focus:outline-none focus:ring-2 focus:ring-sky-500 transition"
            />
          </div>

          <input
            name="travelers"
            type="number"
            placeholder="Number of Travelers"
            value={formData.travelers}
            onChange={handleChange}
            min="1"
            className="w-full px-4 py-3 rounded-lg border border-gray-200 bg-white/70 focus:outline-none focus:ring-2 focus:ring-sky-500 transition"
          />

          <textarea
            name="notes"
            placeholder="Special Requests or Notes"
            rows={4}
            value={formData.notes}
            onChange={handleChange}
            className="w-full px-4 py-3 rounded-lg border border-gray-200 bg-white/70 focus:outline-none focus:ring-2 focus:ring-sky-500 transition"
          ></textarea>

          <button
            type="submit"
            className="w-full py-4 mt-4 text-lg font-semibold rounded-lg text-white bg-sky-600 hover:bg-sky-700 transition-all duration-300 shadow-lg hover:shadow-xl hover:-translate-y-0.5"
          >
            Submit Reservation
          </button>
        </form>
      </div>
    </main>
  );
}
